<template>
  <div class="container">
    <div class="card card-login mx-auto mt-5">
      <div class="card-header">Login</div>
      <div class="card-body">
        <form>
          <div class="form-group">
            <div class="form-label-group">
              <input
                type="email"
                class="form-control"
                placeholder="Email address"
                required="required"
                autofocus="autofocus"
                v-model="email"
              >
            </div>
          </div>
          <div class="form-group">
            <div class="form-label-group">
              <input
                type="password"
                class="form-control"
                placeholder="Password"
                required="required"
                v-model="password"
              >
            </div>
          </div>
          <input type="submit" class="btn btn-primary btn-block" v-on:click="login" value="Login">
        </form>
      </div>
    </div>
  </div>
</template>
<script>
import firebase from "firebase";
export default {
  name: "login",
  data: function() {
    return {
      email: "",
      password: ""
    };
  },
  methods: {
    login: function(e) {
      firebase
        .auth()
        .signInWithEmailAndPassword(this.email, this.password)
        .then(
          user => {
            this.$router.go({ path: this.$router.path });
          },
          err => {
            alert("Opps. " + err.message);
          }
        );
    }
  }
};
</script>




<style scoped>
.card-login {
  max-width: 25rem;
  margin: 20px 0;
}

.card-register {
  max-width: 40rem;
}

.form-label-group {
  position: relative;
}
</style>


