<template>
  <form @submit.prevent="addSkill">
    <div class="container">
      <div class="row">
        <div class="col-sm-12">
          <h1>Add Skill</h1>
          <p>Please Enter the skill details.</p>
          <router-link to="/dashboard" class="btn btn-primary">Dashboard</router-link>
          <hr>
          <label for="name">
            <b>Name</b>
          </label>
          <input type="text" id="name" placeholder="Enter Skill Name" required v-model="name">
          <label for="Key">
            <b>Key Value</b>
          </label>
          <input type="text" id="Key" placeholder="Enter Skill Keys" required v-model="key_value">

          <hr>
          <button type="submit" class="registerbtn">Add Skill</button>
        </div>
      </div>
    </div>
  </form>
</template>

<script>
import db from "./firebaseInit";
export default {
  name: "add_skill",
  data() {
    return {
      name: null,
      key_value: null
    };
  },
  methods: {
    addSkill() {
      db.collection("skills")
        .add({
          name: this.name,
          key_value: this.key_value
        })
        .then(docRef => {
          this.$router.push("./dashboard");
        });
    }
  }
};
</script>
<style scoped>
* {
  box-sizing: border-box;
}

/* Add padding to containers */
.container {
  padding: 16px;
}

/* Full-width input fields */
input[type="text"],
input[type="password"] {
  width: 100%;
  padding: 15px;
  margin: 5px 0 22px 0;
  display: inline-block;
  border: none;
  background: #f1f1f1;
}

input[type="text"]:focus,
input[type="password"]:focus {
  background-color: #ddd;
  outline: none;
}

/* Overwrite default styles of hr */
hr {
  border: 1px solid #f1f1f1;
  margin-bottom: 25px;
}

/* Set a style for the submit/register button */
.registerbtn {
  background-color: #4caf50;
  color: white;
  padding: 16px 20px;
  margin: 8px 0;
  border: none;
  cursor: pointer;
  width: 100%;
  opacity: 0.9;
}

.registerbtn:hover {
  opacity: 1;
}

/* Add a blue text color to links */
a {
  color: dodgerblue;
}

/* Set a grey background color and center the text of the "sign in" section */
.signin {
  background-color: #f1f1f1;
  text-align: center;
}
</style>


