<template>
  <form @submit.prevent="addProject">
    <div class="container">
      <div class="row">
        <div class="col-sm-12">
          <h1>Add Project</h1>
          <p>Please Enter the project details.</p>
          <router-link to="/dashboard" class="btn btn-primary">Dashboard</router-link>
          <hr>
          <div class="row">
            <div class="col-sm-12 col-md-6">
              <label for="title">
                <b>Title</b>
              </label>
              <input
                type="text"
                id="title"
                placeholder="Enter Project Title"
                required
                v-model="title"
              >
            </div>
            <div class="col-sm-12 col-md-6">
              <label for="desc">
                <b>Description</b>
              </label>
              <input
                type="text"
                id="desc"
                placeholder="Enter Project Description"
                required
                v-model="desc"
              >
            </div>
            <div class="col-sm-12 col-md-6">
              <label for="team">
                <b>Team Size</b>
              </label>
              <input type="text" id="team" placeholder="Enter Team Size" required v-model="team">
            </div>
            <div class="col-sm-12 col-md-6">
              <label for="dura">
                <b>Duration</b>
              </label>
              <input type="text" id="dura" placeholder="Enter Duration" required v-model="duration">
            </div>
            <div class="col-sm-12 col-md-6">
              <label for="role">
                <b>Role</b>
              </label>
              <input type="text" id="role" placeholder="Enter your Role" required v-model="role">
            </div>
            <div class="col-sm-12 col-md-6">
              <label for="skill">
                <b>Skill</b>
              </label>
              <input
                type="text"
                id="skill"
                placeholder="Enter Projects Skills"
                required
                v-model="skill"
              >
            </div>
          </div>

          <label for="address">
            <b>Address</b>
          </label>
          <input
            type="text"
            id="address"
            placeholder="Enter project Address"
            required
            v-model="address"
          >
          <hr>
          <button type="submit" class="registerbtn">Add Project</button>
        </div>
      </div>
    </div>
  </form>
</template>

<script>
import db from "./firebaseInit";
export default {
  name: "add_project",
  data() {
    return {
      title: null,
      desc: null,
      team: null,
      duration: null,
      role: null,
      skill: null,
      address: null
    };
  },
  methods: {
    addProject() {
      db.collection("projects")
        .add({
          title: this.title,
          description: this.desc,
          team_size: this.team,
          duration: this.duration,
          role: this.role,
          skill: this.skill,
          address: this.address
        })
        .then(docRef => {
          this.$router.push("./dashboard");
        });
    }
  }
};
</script>

<style scoped>
* {
  box-sizing: border-box;
}

/* Add padding to containers */
.container {
  padding: 16px;
}

/* Full-width input fields */
input[type="text"],
input[type="password"] {
  width: 100%;
  padding: 15px;
  margin: 5px 0 22px 0;
  display: inline-block;
  border: none;
  background: #f1f1f1;
}

input[type="text"]:focus,
input[type="password"]:focus {
  background-color: #ddd;
  outline: none;
}

/* Overwrite default styles of hr */
hr {
  border: 1px solid #f1f1f1;
  margin-bottom: 25px;
}

/* Set a style for the submit/register button */
.registerbtn {
  background-color: #4caf50;
  color: white;
  padding: 16px 20px;
  margin: 8px 0;
  border: none;
  cursor: pointer;
  width: 100%;
  opacity: 0.9;
}

.registerbtn:hover {
  opacity: 1;
}

/* Add a blue text color to links */
a {
  color: dodgerblue;
}

/* Set a grey background color and center the text of the "sign in" section */
.signin {
  background-color: #f1f1f1;
  text-align: center;
}
</style>


