<template>
  <div>
    <div class="headTag">
      <h2>Projects details</h2>
    </div>
    <div class="container">
      <div class="row">
        <div class="col-sm-12 col-md-6" v-for="project in projects" v-bind:key="project.id">
          <div class="rsInit">
            <div class="card mb-3">
              <h5 class="card-header">{{ project.name }}</h5>
              <div class="card-body">
                <div class="row">
                  <div class="col-sm-12 col-md-4">
                    <h5 class="card-titles">Description:</h5>
                  </div>
                  <div class="col-sm-12 col-md-8">
                    <p class="card-text">{{ project.description }}</p>
                  </div>
                  <div class="col-sm-12 col-md-4">
                    <h5 class="card-title">Team Size:</h5>
                  </div>
                  <div class="col-sm-12 col-md-8">
                    <p class="card-text">{{ project.team_size }}</p>
                  </div>
                  <div class="col-sm-12 col-md-4">
                    <h5 class="card-title">Duration:</h5>
                  </div>
                  <div class="col-sm-12 col-md-8">
                    <p class="card-text">{{ project.duration }}</p>
                  </div>
                  <div class="col-sm-12 col-md-4">
                    <h5 class="card-title">Role:</h5>
                  </div>
                  <div class="col-sm-12 col-md-8">
                    <p class="card-text">{{ project.role }}</p>
                  </div>
                  <div class="col-sm-12 col-md-4">
                    <h5 class="card-title">Skill:</h5>
                  </div>
                  <div class="col-sm-12 col-md-8">
                    <p class="card-text">{{ project.skill }}</p>
                  </div>
                  <div class="col-sm-12 col-md-4">
                    <h5 class="card-title">Address:</h5>
                  </div>
                  <div class="col-sm-12 col-md-8">
                    <p class="card-text">
                      <a
                        class="btn btn-success"
                        :href="project.link"
                        target="_black"
                      >See Details</a>
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import db from "./firebaseInit";
export default {
  name: "project",
  data() {
    return {
      projects: []
    };
  },
  created() {
    db.collection("projects")
      .get()
      .then(QuerySnapShot => {
        QuerySnapShot.forEach(doc => {
          const data = {
            id: doc.id,
            project_id: doc.data().project_id,
            name: doc.data().title,
            description: doc.data().description,
            role: doc.data().role,
            skill: doc.data().skill,
            team_size: doc.data().team_size,
            duration: doc.data().duration,
            link: doc.data().address
          };
          this.projects.push(data);
        });
      });
  }
};
</script>

<style scoped>
.card-content p {
  font-size: 20px;
}
.headTag h2 {
  font-size: 3rem;
  font-weight: 400;
  color: rgb(226, 145, 145);
  text-align: center;
  text-shadow: 1px 2px 1px gray;
}
.rsInit:hover {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
</style>


