<template>
  <div>
    <div class="headTag">
      <h2>Education details</h2>
    </div>
    <div class="container">
      <div class="row">
        <div class="col-sm-12 col-md-6" v-for="edu in educations" v-bind:key="edu.id">
          <div class="card rsInit">
            <div class="card">
              <h5 class="card-header">{{ edu.level }}</h5>
              <div class="card-body">
                <div class="row">
                  <div class="col-sm-12 col-md-4">
                    <h5 class="card-title">Collage:</h5>
                  </div>
                  <div class="col-sm-12 col-md-8">
                    <p class="card-text">{{ edu.name }}</p>
                  </div>
                  <div class="col-sm-12 col-md-4">
                    <h5 class="card-title">Board Name:</h5>
                  </div>
                  <div class="col-sm-12 col-md-8">
                    <p class="card-text">{{ edu.board }}</p>
                  </div>
                  <div class="col-sm-12 col-md-4">
                    <h5 class="card-title">Passing Year:</h5>
                  </div>
                  <div class="col-sm-12 col-md-8">
                    <p class="card-text">{{ edu.pass_year }}</p>
                  </div>
                  <div class="col-sm-12 col-md-4">
                    <h5 class="card-title">Stream:</h5>
                  </div>
                  <div class="col-sm-12 col-md-8">
                    <p class="card-text">{{ edu.stream }}</p>
                  </div>
                  <div class="col-sm-12 col-md-4">
                    <h5 class="card-title">Percentage:</h5>
                  </div>
                  <div class="col-sm-12 col-md-8">
                    <p class="card-text">{{ edu.percentage }}</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import db from "./firebaseInit";
export default {
  name: "education",
  data() {
    return {
      educations: []
    };
  },
  created() {
    db.collection("education")
      .get()
      .then(QuerySnapShot => {
        QuerySnapShot.forEach(doc => {
          const data = {
            id: doc.id,
            level: doc.data().level,
            name: doc.data().collage_name,
            board: doc.data().board,
            pass_year: doc.data().year_pass,
            stream: doc.data().stream,
            percentage: doc.data().percentage
          };
          this.educations.push(data);
        });
      });
  }
};
</script>

<style scoped>
.headTag h2 {
  font-size: 3rem;
  font-weight: 400;
  color: rgb(226, 145, 145);
  text-align: center;
  text-shadow: 1px 2px 1px gray;
}
.rsInit{
  margin: 20px 0;
}
.rsInit:hover {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.card-content h4 {
  font-size: 1.5rem;
  padding: 10px;
  margin: 0;
  font-weight: 400;
  color: #444;
}
.card-content p {
  font-size: 1.3rem;
  padding: 10px;
  margin: 0;
  font-weight: 400;
  color: #444;
}
</style>


