              <p class="card-text">All skills details</p>
              <router-link to="/add_skill" class="btn btn-success">Add Skill</router-link>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import db from "./firebaseInit";
export default {
  name: "project",
  data() {
    return {};
  }
};
</script>

<style scoped>
.card-content p {
  font-size: 20px;
}
.rsInit {
  margin: 10px 0;
}

.rsInit:hover {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
</style>


