      <div class="text-show">
        <h1>Ram Charan Chauhan</h1>
        <p>Web Developer</p>
        <a href="https://www.facebook.com/R.S.ChauhanS" target="_blank">
          <i class="fab fa-facebook rs"></i>
        </a>
        <a href="https://www.linkedin.com/in/ramcharan-chauhan-70a062132/" target="_blank">
          <i class="fab fa-linkedin rs"></i>
        </a>
        <a href="https://github.com/srs0447" target="_blank">
          <i class="fab fa-github rs"></i>
        </a>
        <a href="https://twitter.com/RamcharanChau13" target="_blank">
          <i class="fab fa-twitter rs"></i>
        </a>
