            Information Technology from Kamla Nehru Institute of Technology, Sultanpur in June 2019.
            And also completed Diploma in 2016 from Govt. Polytechnic Mirzapur in Information Technology.
            I'm learning in field of web development and machine learning.
            I like to hangout with friends, coding, watching news sometimes and learning new Technology."
          </p>
          <div class="status">
            <h2>
              <span class="text-success" style="font-size:18px; font-weight:500;">Status:</span>
              <span style="font-size:18px; font-weight:500;">Web Developer at Yashuss Unlimited</span>
            </h2>
            <h2>
              <span class="text-success" style="font-size:18px; font-weight:500;">Field:</span>
              <span style="font-size:18px; font-weight:500;">Web Development</span>
            </h2>
          </div>
        </div>
