            <p>Education Details and description.</p>
            <div class="linkbtn">
              <router-link to="/education" class="btn btn-danger">
                <i class="fas fa-external-link-alt"></i>
              </router-link>
            </div>
          </div>
        </div>
        <div class="col-sm-12 col-md-4">
          <div class="box">
            <span class="logoInit">
              <i class="fas fa-tasks se-icon"></i>
            </span>

            <h3>Project's</h3>
            <p>Project's details and description.</p>
            <div class="linkbtn">
              <router-link to="/project" class="btn btn-danger">
                <i class="fas fa-external-link-alt"></i>
              </router-link>
            </div>
          </div>
        </div>
        <div class="col-sm-12 col-md-4">
          <div class="box">
            <span class="logoInit">
              <i class="fas fa-cog se-icon"></i>
            </span>

            <h3>Skills</h3>
            <p>Skills deatils and description</p>
            <div class="linkbtn">
              <router-link to="/skill" class="btn btn-danger">
                <i class="fas fa-external-link-alt"></i>
              </router-link>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "about",
  data() {
    return {};
  }
};
