<style scoped>
body {
  overflow-x: hidden;
}

.slide {
  position: relative;
  padding: 25vh 10%;
  min-height: 100vh;
  width: 100vw;
  box-sizing: border-box;
  transform-style: inherit;
}
.header {
  text-align: center;
  font-size: 175%;
  color: #fff;
  text-shadow: 0 3px 2px rgb(66, 65, 65);
}
#title {
  background-image: linear-gradient(
      to bottom,
      rgba(245, 246, 252, 0.089),
      rgb(12, 12, 12)
    ),
    url("../assets/rs1.png");
  z-index: 2;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  width: 100%;
  overflow-x: hidden;
}
#title .text-show {
  top: 10%;
  left: 30%;
  width: 60%;
}
#title .text-show a {
  font-size: 50px;
  padding: 10px;
  color: #fff;
}
#title .text-show a:hover {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  border-radius: 50%;
  font-size: 30px;
  padding: 15px;
  transition: all 1s ease;
}
@media screen and (max-width: 560px) {
  #title .text-show {
    font-size: 25px;
  }
  #title .text-show h1 {
    font-size: 30px;
  }
  #title .text-show a {
    padding: 0;
    font-size: 40px;
  }
}
.arrow {
  text-align: center;
  margin: 8% 0;
}
.arrow a {
  color: #fff;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  border-radius: 50%;
  font-size: 35px;
  padding: 15px;
}
.bounce {
  -moz-animation: bounce 2s infinite;
  -webkit-animation: bounce 2s infinite;
  animation: bounce 2s infinite;
}

@keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-30px);
  }
  60% {
    transform: translateY(-15px);
  }
}

.container-section {
  background: #f2f2f2;
  padding: 30px;
  text-align: center;
}
.se-icon {
  font-size: 40px;
  color: white;
  padding: 5px;
}
.profileImage {
  padding: 10px;
  float: right;
}
.profileImage img {
  padding: 10px;
  max-width: 300px;
  max-height: 300px;
}
@media screen and (max-width: 560px) {
  .profileImage {
    padding: 10px;
    float: left;
  }
  body {
    overflow-x: hidden;
  }
}
.box {
  background-image: linear-gradient(
    to bottom right,
    rgb(15, 136, 250),
    rgb(122, 212, 235)
  );
  padding: 25px;
  margin-top: 40px;
  position: relative;
  margin-bottom: 10px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  border-radius: 10px;
}
.box h3 {
  padding: 10px;
  margin-top: 20px;
  font-weight: 700;
  color: #fff;
}
.box p {
  color: #fff;
  font-weight: 400;
  font-size: 18px;
}
.box:hover .logoInit {
  transform: rotate(360deg);
  transition: transform 1.5s;
}
#about {
  margin-top: 40px;
  padding: 10px;
}

.linkbtn {
  left: 0;
  width: 100%;
  text-align: center;
}
.linkbtn:hover .btn {
  border-radius: 50%;
  transition: all 0.5s ease-in-out;
}

.logoInit {
  position: absolute;
  padding: 15px;
  border-radius: 100%;
  border: 4px solid white;

  top: -40px;
  left: 38%;
  background: rgb(23, 107, 233);
}
</style>




