<template>
  <div>
    <nav class="navbar navbar-expand-md bg-dark navbar-dark">
      <!-- Brand -->
      <router-link to="/" class="navbar-brand">Ramcharan Chauhan</router-link>

      <!-- Toggler/collapsibe Button -->
      <button
        class="navbar-toggler"
        type="button"
        data-toggle="collapse"
        data-target="#collapsibleNavbar"
      >
        <span class="navbar-toggler-icon"></span>
      </button>

      <!-- Navbar links -->
      <div class="collapse navbar-collapse" id="collapsibleNavbar">
        <ul class="navbar-nav navbar-right">
          <li class="nav-item">
            <router-link to="/" class="nav-link">Home</router-link>
          </li>
          <li class="nav-item">
            <router-link to="/education" class="nav-link">Education</router-link>
          </li>
          <li class="nav-item">
            <router-link to="/project" class="nav-link">Project</router-link>
          </li>
          <li class="nav-item">
            <router-link to="/skill" class="nav-link">Skill</router-link>
          </li>
          <li class="nav-item">
            <a href="http://gradevision.in" target="_blank" class="nav-link">Blog</a>
          </li>
          <li class="nav-item" v-if="isLoggedIn">
            <router-link to="/dashboard" class="nav-link">Dashboard</router-link>
          </li>
          <li class="nav-item" v-if="isLoggedIn">
            <a class="nav-link" v-on:click="logout" href="#">Logout</a>
          </li>
        </ul>
      </div>
    </nav>
  </div>
</template>
<script>
import firebase from "firebase";
export default {
  name: "header",
  data() {
    return {
      isLoggedIn: false,
      currentUser: false
    };
  },
  created() {
    if (firebase.auth().currentUser) {
      this.isLoggedIn = true;
      this.currentUser = firebase.auth().currentUser.email;
    }
  },
  methods: {
    logout: function() {
      firebase
        .auth()
        .signOut()
        .then(() => {
          this.$router.go({ path: this.$router.path });
        });
    }
  }
};
</script>
<style scoped>
body {
  overflow-x: hidden;
}
.bg-dark {
  background-color: #30abd8 !important;
}
.bg-dark .nav-link {
  color: #fff;
}
</style>





