<template>
  <div>
    <div class="headTag">
      <h2>Skills details</h2>
    </div>
    <div class="container">
      <div class="row">
        <div class="col-sm-6 col-md-3" v-for="skill in skills" v-bind:key="skill.id">
          <div class="card mb-3 mt-3">
            <div class="card-header text-center shadow">{{ skill.name.toUpperCase() }}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import db from "./firebaseInit";
export default {
  name: "skill",
  data() {
    return {
      skills: []
    };
  },
  created() {
    db.collection("skills")
      .get()
      .then(QuerySnapShot => {
        QuerySnapShot.forEach(doc => {
          const data = {
            id: doc.id,
            name: doc.data().name,
            description: doc.data().key_value
          };
          this.skills.push(data);
        });
      });
  }
};
</script>

<style scoped>
.card-content p {
  font-size: 20px;
}
.headTag h2 {
  font-size: 3rem;
  font-weight: 400;
  color: rgb(226, 145, 145);
  text-align: center;
  text-shadow: 1px 2px 1px gray;
}
.rsInit:hover {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
</style>


