import Vue from "vue";
import Router from "vue-router";
import About from "@/components/About";
import Project from "@/components/project";
import Skill from "@/components/skill";
import Education from "@/components/education";
import Dashboard from "@/components/dashboard";
import AddSkill from "@/components/add_skill";
import AddEducation from "@/components/add_education";
import AddProject from "@/components/add_project";
import Login from "@/components/login";
import firebase from "firebase";

Vue.use(Router);

let router = new Router({
  routes: [
    {
      path: "/",
      name: "about",
      component: About
    },
    {
      path: "/project",
      name: "project",
      component: Project
    },
    {
      path: "/education",
      name: "education",
      component: Education
    },
    {
      path: "/skill",
      name: "skill",
      component: Skill
    },
    {
      path: "/dashboard",
      name: "dashoard",
      component: Dashboard,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: "/add_skill",
      name: "add_skill",
      component: AddSkill,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: "/add_project",
      name: "add_project",
      component: AddProject,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: "/add_education",
      name: "add_education",
      component: AddEducation,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: "/login",
      name: "login",
      component: Login,
      meta: {
        requiresGuest: true
      }
    }
  ]
});
router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    if (!firebase.auth().currentUser) {
      next({
        path: "/login",
        query: {
          redirect: to.fullPath
        }
      });
    } else {
      next();
    }
  } else if (to.matched.some(record => record.meta.requiresGuest)) {
    if (firebase.auth().currentUser) {
      next({
        path: "/dashboard",
        query: {
          redirect: to.fullPath
        }
      });
    } else {
      next();
    }
  } else {
    next();
  }
});


export default router;



