<template>
  <form @submit.prevent="addEducation">
    <div class="container">
      <div class="row">
        <div class="col-sm-12">
          <h1>Add Education</h1>
          <p>Please Enter the Education details.</p>
          <router-link to="/dashboard" class="btn btn-primary">Dashboard</router-link>
          <hr>
          <div class="row">
            <div class="col-sm-12 col-md-6">
              <label for="board">
                <b>Board</b>
              </label>
              <input type="text" id="board" placeholder="Enter your board" required v-model="board">
            </div>
            <div class="col-sm-12 col-md-6">
              <label for="col">
                <b>Collage Name</b>
              </label>
              <input
                type="text"
                id="col"
                placeholder="Enter Collage Name"
                required
                v-model="collage_name"
              >
            </div>
            <div class="col-sm-12 col-md-6">
              <label for="level">
                <b>Level</b>
              </label>
              <input type="text" id="level" placeholder="Enter Level " required v-model="level">
            </div>
            <div class="col-sm-12 col-md-6">
              <label for="stream">
                <b>Stream</b>
              </label>
              <input type="text" id="stream" placeholder="Enter Stream" required v-model="stream">
            </div>
            <div class="col-sm-12 col-md-6">
              <label for="year">
                <b>Year Of Passing</b>
              </label>
              <input
                type="text"
                id="year"
                placeholder="Year Of Passing"
                required
                v-model="year_pass"
              >
            </div>
            <div class="col-sm-12 col-md-6">
              <label for="perc">
                <b>Percentage</b>
              </label>
              <input
                type="text"
                id="perc"
                placeholder="Enter Percentage"
                required
                v-model="percentage"
              >
            </div>
          </div>
          <hr>
          <button type="submit" class="registerbtn">Add Education</button>
        </div>
      </div>
    </div>
  </form>
</template>

<script>
import db from "./firebaseInit";
export default {
  name: "add_education",
  data() {
    return {
      board: null,
      collage_name: null,
      level: null,
      percentage: null,
      stream: null,
      year_pass: null
    };
  },
  methods: {
    addEducation() {
      db.collection("education")
        .add({
          board: this.board,
          collage_name: this.collage_name,
          level: this.level,
          percentage: this.percentage,
          stream: this.stream,
          year_pass: this.year_pass
        })
        .then(docRef => {
          this.$router.push("./dashboard");
        });
    }
  }
};
</script>

<style scoped>
* {
  box-sizing: border-box;
}

/* Add padding to containers */
.container {
  padding: 16px;
}

/* Full-width input fields */
input[type="text"],
input[type="password"] {
  width: 100%;
  padding: 15px;
  margin: 5px 0 22px 0;
  display: inline-block;
  border: none;
  background: #f1f1f1;
}

input[type="text"]:focus,
input[type="password"]:focus {
  background-color: #ddd;
  outline: none;
}

/* Overwrite default styles of hr */
hr {
  border: 1px solid #f1f1f1;
  margin-bottom: 25px;
}

/* Set a style for the submit/register button */
.registerbtn {
  background-color: #4caf50;
  color: white;
  padding: 16px 20px;
  margin: 8px 0;
  border: none;
  cursor: pointer;
  width: 100%;
  opacity: 0.9;
}

.registerbtn:hover {
  opacity: 1;
}

/* Add a blue text color to links */
a {
  color: dodgerblue;
}

/* Set a grey background color and center the text of the "sign in" section */
.signin {
  background-color: #f1f1f1;
  text-align: center;
}
</style>


